import React, { useContext, useEffect, useMemo, useState } from "react";
import {
  Box,
  Grid,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  InputLabel,
  SxProps,
  Switch,
  TextField,
  Theme,
  Button,
} from "@mui/material";
import AppContext from "../../context/AppContext";
import { Item } from "../../types";
import DemoItem from "../Item";
import ColorPicker from "./ColorPicker";
import {
  Save as SaveIcon,
  Close as CloseIcon,
  FileCopyOutlined as FileCopyOutlinedIcon,
} from "@mui/icons-material";
import ItemPciker from "./ItemPicker";
import PagePciker from "./PagePicker";

const ItemDialog = () => {
  const {
    itemDb,
    isItemEditor,
    toggleItemEditor,
    saveItem,
    itemIdInEditor: itemId,
    setItemIdInEditor,
  } = useContext(AppContext);

  const [item, setItem] = useState<Item>(DEFAULT_STATE);

  const handleChange = (field: keyof Item, v: any) => {
    setItem((prev) => ({
      ...prev,
      [field]: v,
    }));
  };

  const isDirty = useMemo(
    () =>
      JSON.stringify(item) !==
      JSON.stringify(itemId in itemDb ? itemDb[itemId] : DEFAULT_STATE),
    [item, itemDb, itemId]
  );

  useEffect(() => {
    setItem((prev) => {
      if (itemId && itemId in itemDb) {
        return itemDb[itemId];
      }
      return DEFAULT_STATE;
    });
  }, [itemId, itemDb]);

  return (
    <Dialog open={isItemEditor} fullWidth onClose={() => toggleItemEditor()}>
      <DialogTitle>編輯詞語 - {item.id ? item.text : "新詞語"}</DialogTitle>
      <DialogContent>
        <Box sx={contentSx}>
          <ItemPciker
            label="詞語"
            value={itemId}
            onChange={(v: string | null) => setItemIdInEditor(v)}
          />
          <Box sx={demoContainerSx}>
            <DemoItem item={item} demo />
          </Box>
          <Grid container rowGap={2}>
            <Grid item xs={4}>
              <InputLabel>詞語</InputLabel>
            </Grid>
            <Grid item xs={8}>
              <TextField
                label="詞語"
                value={item.text}
                fullWidth
                onChange={({ target: { value } }) =>
                  handleChange("text", value)
                }
                size="small"
              />
            </Grid>
            <Grid item xs={4}>
              <InputLabel>圖片</InputLabel>
            </Grid>
            <Grid item xs={8}>
              <TextField
                label="圖片網址"
                value={item.img ?? ""}
                fullWidth
                onChange={({ target: { value } }) => handleChange("img", value)}
                size="small"
              />
            </Grid>
            <Grid item xs={4}>
              <InputLabel>背景顏色</InputLabel>
            </Grid>
            <Grid item xs={8}>
              <ColorPicker
                value={item.backgroundColor ?? null}
                defaultValue="white"
                onChange={(e) => handleChange("backgroundColor", e ?? "white")}
              />
            </Grid>
            <Grid item xs={4}>
              <InputLabel>文字顏色</InputLabel>
            </Grid>
            <Grid item xs={8}>
              <ColorPicker
                value={item.textColor ?? null}
                defaultValue="black"
                onChange={(e) => handleChange("textColor", e ?? "black")}
              />
            </Grid>
            <Grid item xs={4}>
              <InputLabel>文字底色</InputLabel>
            </Grid>
            <Grid item xs={8}>
              <ColorPicker
                value={item.textBackgroundColor ?? null}
                defaultValue="rgba(255,255,255,0)"
                onChange={(e) =>
                  handleChange(
                    "textBackgroundColor",
                    e ?? "rgba(255,255,255,0)"
                  )
                }
              />
            </Grid>
            <Grid item xs={4}>
              <InputLabel>跳到頁面</InputLabel>
            </Grid>
            <Grid item xs={8}>
              <PagePciker
                label="跳到頁面"
                value={item.pageLink ?? ""}
                onChange={(v) => handleChange("pageLink", v)}
              />
            </Grid>
            {Boolean(item.pageLink) && (
              <>
                <Grid item xs={4}>
                  <InputLabel>點擊時加到句子？</InputLabel>
                </Grid>
                <Grid item xs={8}>
                  <Switch
                    checked={item.typeIn ?? true}
                    onChange={({ target: { checked } }) =>
                      handleChange("typeIn", checked)
                    }
                    size="small"
                  />
                </Grid>
              </>
            )}
          </Grid>
        </Box>
      </DialogContent>
      <DialogActions sx={actionSx}>
        <Box>
          <Button
            startIcon={<FileCopyOutlinedIcon />}
            variant="outlined"
            color="info"
            onClick={() => saveItem(item, true)}
            disabled={!isDirty}
          >
            另存新詞
          </Button>
        </Box>
        <Box>
          <Button
            startIcon={<SaveIcon />}
            variant="contained"
            onClick={() => saveItem(item)}
            disabled={!isDirty}
          >
            儲存
          </Button>
          <Button
            startIcon={<CloseIcon />}
            variant="outlined"
            color="inherit"
            onClick={() => toggleItemEditor()}
          >
            取消
          </Button>
        </Box>
      </DialogActions>
    </Dialog>
  );
};

export default ItemDialog;

const DEFAULT_STATE: Item = {
  id: "",
  text: "",
};

const contentSx: SxProps<Theme> = {
  py: 1,
  display: "flex",
  flexDirection: "column",
  gap: 1,
  alignItems: "center",

  "& > div": {
    display: "flex",
    justifyContent: "space-between",
    width: "100%",
  },
};

const demoContainerSx: SxProps<Theme> = {
  width: "100px !important",
  height: 100,
};

const actionSx: SxProps<Theme> = {
  px: 3,
  justifyContent: "space-between",
  "& > div": {
    display: "flex",
    gap: 1,
  },
};
