import React, { useContext, useEffect, useMemo, useState } from "react";
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  SxProps,
  Theme,
  Button,
  Box,
  TextField,
  Autocomplete,
} from "@mui/material";
import {
  Save as SaveIcon,
  Close as CloseIcon,
  FileCopyOutlined as FileCopyOutlinedIcon,
} from "@mui/icons-material";
import AppContext from "../../context/AppContext";
import { Page } from "../../types";
import TableSelector from "./TableSelector";

const PageDialog = () => {
  const { curPage, isPageEditor, itemDb, togglePageEditor, savePage } =
    useContext(AppContext);

  const [state, setState] = useState<Page>(DEFAULT_PAGE);

  useEffect(() => {
    setState(curPage);
  }, [curPage]);

  const handleFieldChange = (field: keyof Page, v: any) => {
    setState((prev) => ({
      ...prev,
      [field]: v,
    }));
  };

  const isDirty = useMemo(
    () => JSON.stringify(state) === JSON.stringify(curPage),
    [state, curPage]
  );

  return (
    <Dialog open={isPageEditor} onClose={togglePageEditor} fullWidth>
      <DialogTitle>編輯此頁 - {curPage.name}</DialogTitle>
      <DialogContent>
        <Box sx={contentSx}>
          <TextField
            label="名稱"
            value={state.name}
            onChange={({ target: { value } }) =>
              handleFieldChange("name", value)
            }
            fullWidth
            size="small"
          />
          <Autocomplete
            freeSolo
            multiple
            fullWidth
            value={state.tags ?? []}
            renderInput={(params) => <TextField {...params} label="Tags" />}
            onChange={(e, v) => handleFieldChange("tags", v)}
            size="small"
            options={[]}
          />
          <Box sx={{ display: "flex", gap: 2, width: "100%" }}>
            <TableSelector
              value={state.dimension}
              onChange={(v) => handleFieldChange("dimension", v)}
            />
          </Box>
          <Autocomplete
            multiple
            fullWidth
            value={
              state.items
                .slice(0, state.dimension.height * state.dimension.width)
                .map((i) => itemDb[i.itemId].text)
                .filter((v) => v) ?? []
            }
            renderInput={(params) => <TextField {...params} label="詞語" />}
            onChange={(e, v) => handleFieldChange("tags", v)}
            size="small"
            options={[]}
            disabled
          />
        </Box>
      </DialogContent>
      <DialogActions sx={actionSx}>
        <Box>
          <Button
            startIcon={<FileCopyOutlinedIcon />}
            variant="outlined"
            color="info"
            onClick={() => savePage(state, true)}
            disabled={isDirty}
          >
            另存新頁
          </Button>
        </Box>
        <Box>
          <Button
            startIcon={<SaveIcon />}
            variant="contained"
            onClick={() => savePage(state)}
            disabled={isDirty}
          >
            儲存
          </Button>
          <Button
            startIcon={<CloseIcon />}
            variant="outlined"
            color="inherit"
            onClick={() => togglePageEditor()}
          >
            取消
          </Button>
        </Box>
      </DialogActions>
    </Dialog>
  );
};

export default PageDialog;

const contentSx: SxProps<Theme> = {
  py: 1,
  display: "flex",
  flexDirection: "column",
  gap: 2,
  alignItems: "center",
};

const DEFAULT_PAGE: Page = {
  id: "",
  name: "",
  dimension: {
    width: 8,
    height: 5,
  },
  items: [],
};

const actionSx: SxProps<Theme> = {
  px: 3,
  justifyContent: "space-between",
  "& > div": {
    display: "flex",
    gap: 1,
  },
};
