import React, { useContext } from "react";
import AppContext from "../context/AppContext";
import { Box, Button, SxProps, Theme } from "@mui/material";
import {
  Backspace as BackspaceIcon,
  PlayCircle as PlayCircleIcon,
  SmartScreen as SmartScreenIcon,
} from "@mui/icons-material";
import WordBtn from "../components/sentence/WordBtn";
import { useNavigate } from "react-router-dom";

const Sentence = () => {
  const { words, backspaceWord, speak } = useContext(AppContext);
  const navigate = useNavigate();

  return (
    <Box sx={rootSx}>
      <Box sx={wordContainerSx}>
        {words.map((word, idx) => (
          <WordBtn key={idx} word={word} />
        ))}
      </Box>
      <Button onClick={backspaceWord} disableRipple>
        <BackspaceIcon />
      </Button>
      <Button onClick={() => speak(words.join(""))} disableRipple>
        <PlayCircleIcon />
      </Button>
      <Button onClick={() => navigate(`/marquee`)} disableRipple>
        <SmartScreenIcon />
      </Button>
    </Box>
  );
};

export default Sentence;

const rootSx: SxProps<Theme> = {
  display: "flex",
  justifyContent: "space-between",
  px: 2,
};

const wordContainerSx: SxProps<Theme> = {
  display: "flex",
  border: "1px solid grey",
  borderRadius: "5px",
  overflow: "scroll",
  flex: 1,
  m: 1,
  minHeight: 53.5,
};
