import React, { useContext, useState } from "react";
import { Box, Button, IconButton, SxProps, Theme } from "@mui/material";
import {
  Settings as SettingsIcon,
  SaveAsOutlined as SaveAsOutlinedIcon,
  SaveOutlined as SaveOutlinedIcon,
  Login as LoginIcon,
} from "@mui/icons-material";
import AppContext from "../context/AppContext";
import PagePciker from "../components/dialog/PagePicker";
import AuthContext from "../AuthContext";
import SettingList from "../components/settings/SettingList";

const FtnRow = () => {
  const [anchorEl, setAnchorEl] = useState<HTMLButtonElement | null>(null);
  const {
    mode,
    pageStack,
    slicePageStack,
    pages,
    loadPage,
    isPageDirty,
    curPage,
    savePage,
  } = useContext(AppContext);
  const { user, login } = useContext(AuthContext);

  return (
    <Box sx={rootSx}>
      <Box sx={breadcrumbSx}>
        {mode === "view" &&
          pageStack.map((pageId, idx) => (
            <React.Fragment key={`bc-${idx}`}>
              {idx !== 0 && <>/</>}
              <Button key={`bc-${idx}`} onClick={() => slicePageStack(idx)}>
                {pages[pageId].name}
              </Button>
            </React.Fragment>
          ))}
        {mode === "edit" && (
          <Box sx={{ width: 250 }}>
            <PagePciker
              label=""
              value={curPage.id}
              onChange={(v) => loadPage(v)}
              disableClearable
            />
          </Box>
        )}
      </Box>
      <Box sx={{ display: "flex", justifyContent: "flex-end", flex: 1 }}>
        {mode === "edit" && isPageDirty && (
          <>
            <IconButton
              size="small"
              color="primary"
              onClick={() => savePage(curPage)}
            >
              <SaveOutlinedIcon />
            </IconButton>
            <IconButton
              size="small"
              color="primary"
              onClick={() =>
                savePage({ ...curPage, name: curPage.name + " - Copy" }, true)
              }
            >
              <SaveAsOutlinedIcon />
            </IconButton>
          </>
        )}
        <Button
          size="small"
          disableRipple
          onClick={(e) => setAnchorEl(e.currentTarget)}
        >
          <SettingsIcon />
        </Button>
        {!user && (
          <IconButton size="small" color="primary" onClick={() => login()}>
            <LoginIcon />
          </IconButton>
        )}
      </Box>
      <SettingList anchorEl={anchorEl} onClose={() => setAnchorEl(null)} />
    </Box>
  );
};

export default FtnRow;

const rootSx: SxProps<Theme> = {
  display: "flex",
  justifyContent: "space-between",
  p: 2,
  background: (t) => t.palette.background.default,
  "& button": {
    fontSize: "0.8em",
  },
};

const breadcrumbSx: SxProps<Theme> = {
  display: "flex",
  "& button": {
    textTransform: "none",
  },
  alignItems: "center",
};
