import React, { useContext, useEffect, useMemo, useState } from "react";
import {
  Box,
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  InputLabel,
  SxProps,
  Theme,
} from "@mui/material";
import AppContext from "../../context/AppContext";
import DemoItem from "../Item";
import ItemPciker from "./ItemPicker";
import { Save as SaveIcon, Close as CloseIcon } from "@mui/icons-material";

const PageItemDialog = () => {
  const {
    pageItemUuid,
    openPageItemEditor,
    setItemIdInEditor,
    savePageItem,
    curPage,
    itemDb,
  } = useContext(AppContext);

  const coordinate = useMemo(() => {
    if (pageItemUuid === null) return [1, 1];
    const idx = curPage.items.findIndex((v) => v.uuid === pageItemUuid);
    return [
      Math.floor(idx / curPage.dimension.width) + 1,
      (idx % curPage.dimension.width) + 1,
    ];
  }, [curPage, pageItemUuid]);

  const [state, setState] = useState<string>("");

  const isDirty = useMemo(() => {
    const idx = curPage.items.findIndex((v) => v.uuid === pageItemUuid);
    return curPage.items[idx]?.itemId === state;
  }, [state, pageItemUuid, curPage]);

  useEffect(() => {
    if (pageItemUuid === null) setState("");
    else {
      setState(() => {
        const idx = curPage.items.findIndex((v) => v.uuid === pageItemUuid);
        return curPage.items[idx].itemId;
      });
    }
  }, [pageItemUuid, curPage]);

  return (
    <Dialog
      open={pageItemUuid !== null}
      onClose={() => openPageItemEditor(null)}
      fullWidth
    >
      <DialogTitle>
        頁面 {curPage.name} 詞語更改 ({coordinate[0]}, {coordinate[1]}){" "}
      </DialogTitle>
      <DialogContent>
        <Box sx={contentSx}>
          <Box sx={demoContainerSx}>
            <DemoItem item={itemDb[state]} demo />
          </Box>
          <Box sx={ftnContainerSx}>
            <InputLabel>詞語</InputLabel>
            <ItemPciker
              label="詞語"
              value={state}
              // @ts-ignore
              onChange={(v) => setState(v)}
            />
          </Box>
        </Box>
      </DialogContent>
      <DialogActions sx={actionSx}>
        <Box>
          <Button
            variant="outlined"
            color="info"
            onClick={() => setItemIdInEditor(state)}
          >
            編輯詞語 - {itemDb[state]?.text}
          </Button>
        </Box>
        <Box>
          <Button
            startIcon={<SaveIcon />}
            variant="contained"
            onClick={() => savePageItem(state, true)}
            disabled={isDirty}
          >
            儲存
          </Button>
          <Button
            startIcon={<CloseIcon />}
            variant="outlined"
            color="inherit"
            onClick={() => openPageItemEditor(null)}
          >
            取消
          </Button>
        </Box>
      </DialogActions>
    </Dialog>
  );
};

export default PageItemDialog;

const contentSx: SxProps<Theme> = {
  py: 1,
  display: "flex",
  flexDirection: "column",
  gap: 1,
  alignItems: "center",
};

const demoContainerSx: SxProps<Theme> = {
  width: 100,
  height: 100,
  display: "flex",
};

const ftnContainerSx: SxProps<Theme> = {
  mt: 2,
  display: "flex",
  justifyContent: "space-between",
  width: "100%",
  alignItems: "center",
  "& label": {
    minWidth: 200,
  },
};

const actionSx: SxProps<Theme> = {
  px: 3,
  justifyContent: "space-between",
  "& > div": {
    display: "flex",
    gap: 1,
  },
};
