import React, { useContext } from "react";
import { Button, SxProps, Theme } from "@mui/material";
import AppContext from "../../context/AppContext";

const WordBtn = ({ word }: { word: string }) => {
  const { speak } = useContext(AppContext);

  return (
    <Button
      sx={wordSx}
      disableRipple
      variant="text"
      onClick={() => speak(word)}
    >
      {word}
    </Button>
  );
};

export default WordBtn;

const wordSx: SxProps<Theme> = {
  cursor: "default",
  m: 0.5,
  minWidth: "max-content",
  fontSize: 18,
};
