import React, { useCallback, useEffect, useState } from "react";
import { useCookies } from "react-cookie";
import { User } from "./types";

interface AuthContextState {}

interface AuthContextProps {
  user: User | undefined;
  authFetch: (input: RequestInfo, options?: RequestInit) => Promise<Response>;
  login: () => void;
  logout: () => void;
}

const AuthContext = React.createContext({} as AuthContextProps);

const API_URL = process.env.REACT_APP_API_URL;

export const AuthContextProvider = ({ children }) => {
  const [loginPopup, setLoginPopup] = useState<Window | null>(null);
  const [cookies, setCookie, removeCookie] = useCookies();

  const authFetch = useCallback(
    (input: RequestInfo, options?: RequestInit): Promise<Response> => {
      return fetch(`${API_URL}${input}`, {
        ...options,
        credentials: "include",
      });
    },
    []
  );

  const login = useCallback(() => {
    authFetch("/login")
      .then((r) => r.json())
      .then((url) => {
        const width = 500;
        const height = 400;
        const left = window.screenX + (window.outerWidth - width) / 2;
        const top = window.screenY + (window.outerHeight - height) / 2.5;

        setLoginPopup(
          window.open(
            url,
            "Login Closer AAC",
            `width=${width},` +
              `height=${height},` +
              `left=${left},` +
              `top=${top}`
          )
        );
      });
  }, [authFetch]);

  const logout = useCallback(() => {
    removeCookie("user", { path: "/", domain: "yuedict.app" });
  }, [removeCookie]);

  useEffect(() => {
    let timer: NodeJS.Timer | null = null;
    if (loginPopup !== null) {
      timer = setInterval(() => {
        if (loginPopup.closed && timer) {
          clearInterval(timer);
        }
        try {
          if (loginPopup?.location.host === window.location.host) {
            loginPopup?.close();
            window.location.reload();
          }
        } catch (e) {
          // ignore error, simply not able to access cross domain content
        }
      }, 500);
    }
    return () => {
      if (timer) {
        clearInterval(timer);
      }
    };
  }, [loginPopup]);

  return (
    <AuthContext.Provider
      value={{
        user: cookies["user"] as User | undefined,
        authFetch,
        login,
        logout,
      }}
    >
      {children}
    </AuthContext.Provider>
  );
};

export default AuthContext;
