import React, { FC, useContext, useMemo } from "react";
import {
  Autocomplete,
  Popper as MuiPopper,
  Typography,
  TextField,
  useMediaQuery,
  MenuItem,
  Box,
} from "@mui/material";
import { autocompleteClasses } from "@mui/material/Autocomplete";
import { useTheme, styled } from "@mui/material/styles";
import { VariableSizeList } from "react-window";
import AppContext from "../../context/AppContext";

const LISTBOX_PADDING = 8;

interface ItemPcikerProps {
  label: string;
  value: string | null;
  onChange: Function;
  showError?: Boolean;
  disabled?: boolean;
}

const ItemPciker: FC<ItemPcikerProps> = ({
  label,
  value,
  onChange,
  showError,
  disabled = false,
}) => {
  const { itemDb } = useContext(AppContext);

  const options = useMemo(
    () =>
      Object.entries(itemDb)
        .filter(([key]) => key)
        .map(([key, { id, text, ...props }]) => ({
          ...props,
          value: id,
          label: text,
        })),
    [itemDb]
  );

  return (
    <Autocomplete
      fullWidth
      disableListWrap
      options={options}
      renderInput={(params) => (
        <TextField
          {...params}
          label={label}
          error={showError ? !value : false}
          size="small"
        />
      )}
      PopperComponent={(props) => (
        <Popper {...props} style={{ width: "600px" }} />
      )}
      filterOptions={(options, state) => {
        return options.filter((option) => {
          const optionString = option.label.toLowerCase();
          const valueString = state.inputValue.toLowerCase();
          let i = 0,
            j = 0;
          while (i < valueString.length && j < optionString.length) {
            if (valueString[i] !== optionString[j]) {
              j += 1;
            } else {
              i += 1;
              j += 1;
            }
          }
          if (i === valueString.length) {
            return true;
          }
          return false;
        });
      }}
      // @ts-ignore
      ListboxComponent={ListboxComponent}
      // @ts-ignore
      renderOption={(props, option) => [props, option]}
      // @ts-ignore
      value={options
        .filter((option) => option.value === value)
        .reduce((acc, cur) => cur, null)}
      onChange={(e, selected) => {
        const ret = typeof selected === "string" ? "" : selected?.value ?? "";
        onChange(ret);
      }}
      disabled={disabled}
    />
  );
};

export default ItemPciker;

// Adapter for react-window
const ListboxComponent = React.forwardRef<
  HTMLDivElement,
  { children: React.ReactNode }
>((props, ref) => {
  const { children, ...other } = props;

  const itemData: any[] = [];
  // @ts-ignore
  children.forEach((item) => {
    itemData.push(item);
    itemData.push(...(item.children || []));
  });

  const theme = useTheme();
  const smUp = useMediaQuery(theme.breakpoints.up("sm"), {
    noSsr: true,
  });

  const itemCount = itemData.length;
  const itemSize = smUp ? 36 : 48;

  const getChildSize = () => {
    return itemSize;
  };

  const getHeight = () => {
    if (itemCount > 8) {
      return 8 * itemSize;
    }
    return itemData.map(getChildSize).reduce((a, b) => a + b, 0);
  };

  const gridRef = useResetCache(itemCount);

  return (
    <div ref={ref}>
      <OuterElementContext.Provider value={other}>
        <VariableSizeList
          itemData={itemData}
          height={getHeight() + 2 * LISTBOX_PADDING}
          width="100%"
          ref={gridRef}
          outerElementType={OuterElementType}
          innerElementType="ul"
          itemSize={(index) => getChildSize()}
          overscanCount={5}
          itemCount={itemCount}
        >
          {renderRow}
        </VariableSizeList>
      </OuterElementContext.Provider>
    </div>
  );
});

function useResetCache(data: any) {
  const ref = React.useRef(null);
  React.useEffect(() => {
    if (ref.current != null) {
      // @ts-ignore
      ref.current.resetAfterIndex(0, true);
    }
  }, [data]);
  return ref;
}

function renderRow(rowProps: any) {
  const { data, index, style } = rowProps;
  const [props, item] = data[index];
  const inlineStyle = {
    ...style,
    top: style.top + LISTBOX_PADDING,
  };

  return (
    <MenuItem
      sx={{
        backgroundColor: item.backgroundColor ?? "rgba(255,255,255,0)",
        display: "flex",
        justifyContent: "space-between",
        width: "100%",
        flex: 1,
      }}
      style={inlineStyle}
      {...props}
    >
      <Box
        sx={{
          backgroundImage: `url(${item.img})`,
          height: "100%",
          width: "2em",
          backgroundPosition: "center",
          backgroundRepeatX: "no-repeat",
          backgroundRepeatY: "no-repeat",
          backgroundSize: "contain",
        }}
      />
      <Typography
        noWrap
        sx={{
          color: item.textColor ?? "black",
          background: item.textBackgroundColor ?? "rgba(255, 255, 255, 0)",
          px: 1,
        }}
      >
        {item.label}
      </Typography>
    </MenuItem>
  );
}

const OuterElementContext = React.createContext({});

const OuterElementType = React.forwardRef<HTMLDivElement>((props, ref) => {
  const outerProps = React.useContext(OuterElementContext);
  return <div ref={ref} {...props} {...outerProps} />;
});

const Popper = styled(MuiPopper)({
  [`& .${autocompleteClasses.listbox}`]: {
    boxSizing: "border-box",
    "& ul": {
      padding: 0,
      margin: 0,
    },
  },
});
