import React, { useContext } from "react";
import { Box, Popover, Divider, Switch, SxProps, Theme } from "@mui/material";
import {
  FormatShapes as FormatShapesIcon,
  DocumentScannerOutlined as DocumentScannerOutlinedIcon,
  Attribution as AttributionIcon,
  Logout as LogoutIcon,
} from "@mui/icons-material";
import AppContext from "../../context/AppContext";
import AuthContext from "../../AuthContext";

interface SettingListProps {
  anchorEl: HTMLButtonElement | null;
  onClose: () => void;
}

const SettingList = ({ anchorEl, onClose }: SettingListProps) => {
  const { user, logout } = useContext(AuthContext);
  const { mode, setMode, togglePageEditor, toggleItemEditor } =
    useContext(AppContext);

  return (
    <Popover
      open={Boolean(anchorEl)}
      anchorEl={anchorEl}
      onClose={onClose}
      slotProps={{
        paper: {
          sx: menuSx,
        },
      }}
      anchorOrigin={{
        vertical: "bottom",
        horizontal: "left",
      }}
    >
      <Box
        sx={{ cursor: "pointer" }}
        onClick={() => {
          togglePageEditor();
          onClose();
        }}
      >
        編輯此頁
        <DocumentScannerOutlinedIcon />
      </Box>
      <Box>
        編輯詞語排版
        <Switch
          checked={mode === "edit"}
          onChange={({ target: { checked } }) => {
            setMode(checked ? "edit" : "view");
            onClose();
          }}
          size="small"
        />
      </Box>
      <Box
        sx={{ cursor: "pointer" }}
        onClick={() => {
          toggleItemEditor();
          onClose();
        }}
      >
        詞語編輯器
        <FormatShapesIcon />
      </Box>
      {user && (
        <Box sx={{ cursor: "pointer" }} onClick={logout}>
          登出
          <LogoutIcon />
        </Box>
      )}
      <Divider sx={{ width: "100%" }} />
      <Box
        sx={{ cursor: "pointer" }}
        onClick={() => {
          window.open("/attribution.html", "_blank");
          onClose();
        }}
      >
        版權相關
        <AttributionIcon />
      </Box>
    </Popover>
  );
};

export default SettingList;

const menuSx: SxProps<Theme> = {
  display: "flex",
  flexDirection: "column",
  alignItems: "center",
  "& > div": {
    display: "flex",
    width: "20ch",
    justifyContent: "space-between",
    alignItems: "center",
    px: 1,
  },
  p: 1,
  gap: 1,
};
