import React, { useCallback } from "react";
import { Dimension } from "../../types";
import { Box, Grid, SxProps, Theme } from "@mui/material";

interface TableSelectorProps {
  value: Dimension;
  onChange: (value: Dimension) => void;
}

const TableSelector = ({ value, onChange }: TableSelectorProps) => {
  const isMatch = useCallback(
    (option: Dimension) =>
      option.width === value.width && option.height === value.height,
    [value]
  );

  return (
    <Box sx={rootSx}>
      {OPTIONS.map(({ width, height }, idx) => (
        <Box
          key={`tableSelector-${idx}`}
          sx={{
            ...optionSx,
            outlineColor: isMatch({ width, height }) ? "black" : "#bbb",
          }}
        >
          <Box sx={buttonSx} onClick={() => onChange({ width, height })}>
            {Array(height)
              .fill(0)
              .map((v, rowIdx) => (
                <Box key={`tableSelector-${idx}-${rowIdx}`}>
                  {Array(width)
                    .fill(0)
                    .map((v, colIdx) => (
                      <Box
                        key={`tableSelector-${idx}-${rowIdx}-${colIdx}`}
                        sx={cellSx}
                      ></Box>
                    ))}
                </Box>
              ))}
          </Box>
        </Box>
      ))}
    </Box>
  );
};

export default TableSelector;

const OPTIONS: Dimension[] = [
  { width: 2, height: 1 },
  { width: 3, height: 1 },
  { width: 2, height: 2 },
  { width: 3, height: 2 },
  { width: 4, height: 4 },
  { width: 8, height: 5 },
];

const rootSx: SxProps<Theme> = {
  display: "flex",
  flexWrap: "wrap",
  flex: 1,
};

const optionSx: SxProps<Theme> = {
  outlineStyle: "solid",
  outlineWidth: 3,
  outlineOffset: -3,
  p: 1,
  flex: "1 1 30%",
  "&:hover": {
    outlineColor: "black",
  },
};

const buttonSx: SxProps<Theme> = {
  display: "flex",
  flexDirection: "column",
  flex: 1,
  justifyContent: "center",
  minHeight: 100,
  cursor: "pointer",
  "& > div": {
    display: "flex",
    flex: 1,
    "& > div": {
      flex: 1,
      border: "1px solid black",
    },
  },
};

const cellSx: SxProps<Theme> = {};
