import React, { FC, useContext, useMemo } from "react";
import {
  Autocomplete,
  Box,
  Popper as MuiPopper,
  Typography,
  TextField,
  useMediaQuery,
  SxProps,
  Theme,
} from "@mui/material";
import { autocompleteClasses } from "@mui/material/Autocomplete";
import { useTheme, styled } from "@mui/material/styles";
import { VariableSizeList } from "react-window";
import AppContext from "../../context/AppContext";

const LISTBOX_PADDING = 8;

interface PagePcikerProps {
  label: string;
  value: string | null;
  onChange: Function;
  showError?: Boolean;
  disabled?: boolean;
  disableClearable?: boolean;
}

const PagePciker: FC<PagePcikerProps> = ({
  label,
  value,
  onChange,
  showError,
  disabled = false,
  disableClearable = false,
}) => {
  const { pages } = useContext(AppContext);

  const options = useMemo(
    () =>
      Object.entries(pages)
        .filter(([key]) => key)
        .map(([key, { id, name }]) => ({
          value: id,
          label: name,
        })),
    [pages]
  );

  return (
    <Box sx={pickerSx}>
      <Autocomplete
        fullWidth
        disableListWrap
        disableClearable={disableClearable}
        options={options}
        renderInput={(params) => (
          <TextField
            {...params}
            label="頁面"
            error={showError ? !value : false}
            size="small"
          />
        )}
        PopperComponent={(props) => (
          <Popper {...props} style={{ width: "600px" }} />
        )}
        filterOptions={(options, state) => {
          return options.filter((option) => {
            const optionString = option.label.toLowerCase();
            const valueString = state.inputValue.toLowerCase();
            let i = 0,
              j = 0;
            while (i < valueString.length && j < optionString.length) {
              if (valueString[i] !== optionString[j]) {
                j += 1;
              } else {
                i += 1;
                j += 1;
              }
            }
            if (i === valueString.length) {
              return true;
            }
            return false;
          });
        }}
        // @ts-ignore
        ListboxComponent={ListboxComponent}
        // @ts-ignore
        renderOption={(props, option) => [props, option]}
        // @ts-ignore
        value={options
          .filter((option) => option.value === value)
          .reduce((acc, cur) => cur, null)}
        onChange={(e, selected) => {
          const ret = typeof selected === "string" ? "" : selected?.value ?? "";
          onChange(ret);
        }}
        disabled={disabled}
      />
    </Box>
  );
};

export default PagePciker;

// Adapter for react-window
const ListboxComponent = React.forwardRef<
  HTMLDivElement,
  { children: React.ReactNode }
>((props, ref) => {
  const { children, ...other } = props;

  const itemData: any[] = [];
  // @ts-ignore
  children.forEach((item) => {
    itemData.push(item);
    itemData.push(...(item.children || []));
  });

  const theme = useTheme();
  const smUp = useMediaQuery(theme.breakpoints.up("sm"), {
    noSsr: true,
  });

  const itemCount = itemData.length;
  const itemSize = smUp ? 36 : 48;

  const getChildSize = () => {
    return itemSize;
  };

  const getHeight = () => {
    if (itemCount > 8) {
      return 8 * itemSize;
    }
    return itemData.map(getChildSize).reduce((a, b) => a + b, 0);
  };

  const gridRef = useResetCache(itemCount);

  return (
    <div ref={ref}>
      <OuterElementContext.Provider value={other}>
        <VariableSizeList
          itemData={itemData}
          height={getHeight() + 2 * LISTBOX_PADDING}
          width="100%"
          ref={gridRef}
          outerElementType={OuterElementType}
          innerElementType="ul"
          itemSize={(index) => getChildSize()}
          overscanCount={5}
          itemCount={itemCount}
        >
          {renderRow}
        </VariableSizeList>
      </OuterElementContext.Provider>
    </div>
  );
});

function useResetCache(data: any) {
  const ref = React.useRef(null);
  React.useEffect(() => {
    if (ref.current != null) {
      // @ts-ignore
      ref.current.resetAfterIndex(0, true);
    }
  }, [data]);
  return ref;
}

function renderRow(props: any) {
  const { data, index, style } = props;
  const dataSet = data[index];
  const inlineStyle = {
    ...style,
    top: style.top + LISTBOX_PADDING,
  };
  return (
    <Typography component="li" {...dataSet[0]} noWrap style={inlineStyle}>
      {dataSet[1].label}
    </Typography>
  );
}

const OuterElementContext = React.createContext({});

const OuterElementType = React.forwardRef<HTMLDivElement>((props, ref) => {
  const outerProps = React.useContext(OuterElementContext);
  return <div ref={ref} {...props} {...outerProps} />;
});

const Popper = styled(MuiPopper)({
  [`& .${autocompleteClasses.listbox}`]: {
    boxSizing: "border-box",
    "& ul": {
      padding: 0,
      margin: 0,
    },
  },
});

const pickerSx: SxProps<Theme> = {
  display: "flex",
  alignItems: "center",
  gap: 1,
};
