import React, { useCallback, useContext, useRef } from "react";
import AppContext from "../context/AppContext";
import { Box, SxProps, Theme } from "@mui/material";
import Item, { ItemRef } from "../components/Item";

const ItemGrids = () => {
  const {
    itemDb,
    curPage,
    mode,
    handleDragStart,
    handleDragOver,
    handleDragEnd,
  } = useContext(AppContext);

  const {
    dimension: { height, width },
    items,
  } = curPage;

  const containerRef = useRef<HTMLDivElement>(null);
  const itemRefs = useRef<ItemRef[]>([]);

  const handleTouchStart = useCallback(
    (e) => {
      if (mode === "view") return;
      console.log("start");
      for (let i = 0; i < itemRefs.current.length; ++i) {
        if (itemRefs.current[i]?.isIntersect(e)) {
          handleDragStart(itemRefs.current[i].uuid ?? "");
          return;
        }
      }
    },
    [handleDragStart]
  );

  const handleTouchMove = useCallback(
    (e: React.TouchEvent) => {
      if (mode === "view") return;
      console.log("move");
      for (let i = 0; i < itemRefs.current.length; ++i) {
        if (itemRefs.current[i]?.isIntersect(e)) {
          handleDragOver(itemRefs.current[i].uuid ?? "");
          return;
        }
      }
    },
    [handleDragOver]
  );

  const handleTouchEnd = useCallback(() => {
    console.log("end");
    handleDragEnd();
  }, [handleDragEnd]);

  return (
    <Box
      ref={containerRef}
      sx={{
        ...rootSx,
        "& > div": {
          flex: `1 1 ${Math.floor(100 / width)}%`,
        },
      }}
      onTouchStart={handleTouchStart}
      onTouchMove={handleTouchMove}
      onTouchEnd={handleTouchEnd}
    >
      {Array(height * width)
        .fill(0)
        .map((v, idx) => (
          <Item
            ref={(el) => {
              if (el) {
                itemRefs.current[idx] = el;
              }
            }}
            key={`cell-${items[idx].uuid}`}
            uuid={curPage.items[idx].uuid}
            item={itemDb[curPage.items[idx].itemId]}
          />
        ))}
    </Box>
  );
};

export default ItemGrids;

const rootSx: SxProps<Theme> = {
  display: "flex",
  flex: 1,
  flexWrap: "wrap",
};
