import React, {
  useCallback,
  useContext,
  useImperativeHandle,
  useRef,
} from "react";
import { Box, SxProps, Theme, Typography } from "@mui/material";
import AppContext from "../context/AppContext";
import { Item as ItemType } from "../types";
import {
  Add as AddIcon,
  DeleteOutlined as DeleteIcon,
  ControlCamera as ControlCameraIcon,
  Edit as EditIcon,
} from "@mui/icons-material";

interface WordGridProps {
  item: ItemType;
  uuid?: string;
  demo?: boolean;
}

export interface ItemRef {
  uuid?: string;
  isIntersect: (e: React.TouchEvent) => boolean;
}

const TMP_DRAGGING_ITEM_ID = "tmp-dragging-item";

const Item = React.memo(
  React.forwardRef<ItemRef, WordGridProps>(
    ({ item, uuid, demo = false }: WordGridProps, forwardedRef) => {
      const {
        addWord,
        mode,
        handleDragStart,
        handleDragOver,
        handleDragEnd,
        emptyItemFromPage,
        openPageItemEditor,
        speak,
        loadPage,
      } = useContext(AppContext);

      const ref = useRef<HTMLDivElement>(null);

      const isIntersect = (e: React.TouchEvent): boolean => {
        if (ref.current === null) return false;
        const { top, bottom, left, right } =
          ref.current.getBoundingClientRect();

        return (
          left < e.touches[0].clientX &&
          e.touches[0].clientX < right &&
          top < e.touches[0].clientY &&
          e.touches[0].clientY < bottom
        );
      }; //, [handleDragOver, uuid, item])

      useImperativeHandle(forwardedRef, () => ({
        uuid,
        isIntersect,
      }));

      const handleClick = () => {
        if (demo) return;
        if (mode === "view") {
          if (item.text && (!item.pageLink || item.typeIn)) {
            addWord(item.text);
          }
          speak(item.text);
          if (item.pageLink) {
            loadPage(item.pageLink, true);
          }
        }
      };

      const handleDelete = () => {
        if (uuid) emptyItemFromPage(uuid);
      };

      const _handleDragStart = useCallback(
        (e) => {
          handleDragStart(uuid ?? "");
          const crt = e.currentTarget.cloneNode(true) as HTMLElement;
          crt.id = TMP_DRAGGING_ITEM_ID;
          crt.style.height = `${
            e.currentTarget.getBoundingClientRect().height
          }px`;
          crt.style.width = `${
            e.currentTarget.getBoundingClientRect().width
          }px`;
          document.body.appendChild(crt);
          e.dataTransfer.setDragImage(
            crt,
            e.clientX - e.currentTarget.getBoundingClientRect().x,
            e.clientY - e.currentTarget.getBoundingClientRect().y
          );
        },
        [handleDragStart, uuid]
      );

      const _handleDragOver = useCallback(
        (e) => {
          e.preventDefault();
          handleDragOver(uuid ?? "");
        },
        [handleDragOver, uuid]
      );

      const _handleDragEnd = useCallback(() => {
        document.getElementById(TMP_DRAGGING_ITEM_ID)?.remove();
        handleDragEnd();
      }, [handleDragEnd]);

      return (
        <Box
          sx={{
            ...rootSx,
            background: item.backgroundColor ?? "white",
          }}
          onClick={handleClick}
          draggable={mode === "edit" && !demo}
          onDragStart={_handleDragStart}
          onDragEnd={_handleDragEnd}
          onDragOver={_handleDragOver}
          ref={ref}
        >
          {item.img && (
            <Box
              sx={{
                ...imgSx,
                backgroundImage: `url(${item.img})`,
              }}
            />
          )}
          <Typography
            sx={{
              ...textSx,
              fontSize: item.fontSize ?? 16,
              color: item.textColor ?? "black",
              backgroundColor:
                item.textBackgroundColor ?? "rgba(255,255,255,0)",
            }}
          >
            {item.text}
          </Typography>
          {!demo && mode === "edit" && (
            <ControlCameraIcon fontSize="small" sx={moveIconSx} />
          )}
          {!demo && mode === "edit" && item.id && (
            <>
              <EditIcon
                fontSize="small"
                sx={editIconSx}
                onClick={() => {
                  openPageItemEditor(uuid ?? null);
                }}
              />
              <DeleteIcon
                fontSize="small"
                sx={deleteIconSx}
                color="error"
                onClick={() => handleDelete()}
              />
            </>
          )}
          {!demo && mode === "edit" && item.id === "" && (
            <AddIcon
              fontSize="small"
              sx={addIconSx}
              onClick={() => openPageItemEditor(uuid ?? null)}
            />
          )}
        </Box>
      );
    }
  ),
  (a, b) => JSON.stringify(a) === JSON.stringify(b)
);

export default Item;

const rootSx: SxProps<Theme> = {
  outline: "2px solid black",
  outlineOffset: "-2px",
  display: "flex",
  alignItems: "center",
  flexDirection: "column",
  justifyContent: "space-around",
  flex: 1,
  position: "relative",
  touchAction: "none",
};

const imgSx: SxProps<Theme> = {
  width: "60%",
  height: "60%",
  backgroundSize: "contain",
  backgroundRepeatX: "no-repeat",
  backgroundRepeatY: "no-repeat",
  backgroundPosition: "center",
};

const moveIconSx: SxProps<Theme> = {
  position: "absolute",
  left: 0,
  top: 0,
  m: 0.5,
  cursor: "grab",
};

const editIconSx: SxProps<Theme> = {
  position: "absolute",
  right: 0,
  top: 0,
  m: 0.5,
  cursor: "pointer",
};

const addIconSx: SxProps<Theme> = {
  position: "absolute",
  right: 0,
  bottom: 0,
  m: 0.5,
  cursor: "pointer",
};

const deleteIconSx: SxProps<Theme> = {
  position: "absolute",
  left: 0,
  bottom: 0,
  m: 0.5,
  cursor: "pointer",
};

const textSx: SxProps<Theme> = {
  p: 0.5,
  borderRadius: 1,
};
