import React, { useCallback, useEffect, useMemo, useState } from "react";
import { Item, Mode, Page } from "../types";
import { v4 as uuidv4 } from "uuid";
import DEFAULT_PAGE from "../defaultPage.json";
import DEFAULT_ITEMS from "../defaultItems.json";

const APP_PREFIX = "closer";

interface AppContextState {
  words: string[];
  db: any;
  itemDb: { [id: string]: Item };
  pages: { [id: string]: Page };
  pageStack: string[];
  curPage: Page;
  _curPage: Page;

  // edit mode
  mode: Mode;
  curDragUuid: string | null;

  // pageEditor
  isPageEditor: boolean;

  // pageItem editor
  pageItemUuid: string | null;

  // item editor
  isItemEditor: boolean;
  itemIdInEditor: string;
}

interface AppContextValue extends AppContextState {
  addWord: (word: string) => void;
  removeWord: (idx: number) => void;
  backspaceWord: () => void;
  clearWords: () => void;
  speak: (str: string) => void;
  loadPage: (id: string, pushToStack?: boolean) => void;
  slicePageStack: (idx: number) => void;

  // edit mode
  isPageDirty: boolean;
  setMode: (mode: Mode) => void;
  handleDragStart: (uuid: string) => void;
  handleDragEnd: () => void;
  handleDragOver: (uuid: string) => void;
  emptyItemFromPage: (uuid: string) => void;

  // page editor
  togglePageEditor: () => void;

  // page Item editor
  openPageItemEditor: (uuid: string | null) => void;
  savePageItem: (id: string, closeEditor?: boolean) => void;
  savePage: (page: Page, saveAs?: boolean) => void;

  // item editor
  toggleItemEditor: () => void;
  setItemIdInEditor: (id: string | null) => void;
  saveItem: (item: Item, saveAs?: boolean) => void;
}

const AppContext = React.createContext<AppContextValue>({} as AppContextValue);

export const AppContextProvider = ({
  children,
}: {
  children: React.ReactElement;
}) => {
  const [state, setState] = useState<AppContextState>(DEFAULT_STATE);

  const addWord = useCallback(
    (word: string) =>
      setState((prev) => ({
        ...prev,
        words: [...prev.words, word],
      })),
    [setState]
  );

  const removeWord = useCallback(
    (idx: number) =>
      setState((prev) => ({
        ...prev,
        words: prev.words.filter((v, _idx) => idx !== _idx),
      })),
    [setState]
  );

  const backspaceWord = useCallback(
    () =>
      setState((prev) => ({
        ...prev,
        words: prev.words.filter((v, idx, self) => idx !== self.length - 1),
      })),
    [setState]
  );

  const clearWords = useCallback(
    () =>
      setState((prev) => ({
        ...prev,
        words: [],
      })),
    [setState]
  );

  const speak = useCallback((str: string) => {
    if ("speechSynthesis" in window) {
      let msg = new SpeechSynthesisUtterance(str);
      msg.lang = "zh-HK";
      window.speechSynthesis.speak(msg);
    }
  }, []);

  const loadPage = useCallback(
    (pageId: string, pushToStack: boolean = false) => {
      setState((prev) => ({
        ...prev,
        pageStack:
          pushToStack && pageId in prev.pages
            ? [...prev.pageStack, pageId]
            : prev.pageStack,
        curPage: pageId in prev.pages ? prev.pages[pageId] : prev.curPage,
        _curPage: pageId in prev.pages ? prev.pages[pageId] : prev.curPage,
      }));
    },
    []
  );

  const slicePageStack = useCallback((idx: number) => {
    setState((prev) => {
      if (idx < 0 && idx >= prev.pageStack.length) return prev;
      return {
        ...prev,
        pageStack: prev.pageStack.slice(0, idx + 1),
        curPage: prev.pages[prev.pageStack[idx]],
        _curPage: prev.pages[prev.pageStack[idx]],
      };
    });
  }, []);

  const isPageDirty = useMemo(
    () => JSON.stringify(state._curPage) !== JSON.stringify(state.curPage),
    [state._curPage, state.curPage]
  );

  const setMode = useCallback(
    (mode: Mode) => {
      setState((prev) => ({ ...prev, mode }));
    },
    [setState]
  );

  const handleDragStart = useCallback(
    (uuid: string) => {
      setState((prev) => ({ ...prev, curDragUuid: uuid }));
    },
    [setState]
  );

  const handleDragEnd = useCallback(
    () =>
      setState((prev) => ({
        ...prev,
        curDragUuid: null,
      })),
    [setState]
  );

  const handleDragOver = useCallback(
    (uuid: string) => {
      if (state.curDragUuid === uuid) return;
      setState((prev) => {
        const curPage = structuredClone(prev.curPage);
        let aIdx = curPage.items.findIndex(
          ({ uuid: _uuid }) => _uuid === prev.curDragUuid
        );
        let bIdx = curPage.items.findIndex(({ uuid: _uuid }) => _uuid === uuid);
        const tmp = curPage.items[aIdx];
        curPage.items[aIdx] = curPage.items[bIdx];
        curPage.items[bIdx] = tmp;
        return {
          ...prev,
          curPage,
        };
      });
    },
    [state.curDragUuid, setState]
  );

  const emptyItemFromPage = useCallback(
    (uuid: string) =>
      setState((prev) => {
        const curPage = structuredClone(prev.curPage);
        curPage.items = curPage.items.map((item) => {
          if (item.uuid === uuid) return { uuid, itemId: "" };
          return item;
        });

        return {
          ...prev,
          curPage,
        };
      }),
    []
  );

  const togglePageEditor = useCallback(
    () =>
      setState((prev) => ({
        ...prev,
        isPageEditor: !prev.isPageEditor,
      })),
    []
  );

  const openPageItemEditor = useCallback(
    (uuid: string | null) =>
      setState((prev) => ({
        ...prev,
        pageItemUuid: uuid,
      })),
    []
  );

  const savePageItem = useCallback(
    (id: string, closeEditor: boolean = false) => {
      setState((prev) => {
        if (prev.pageItemUuid === null) return prev;
        return {
          ...prev,
          pageItemUuid: closeEditor ? null : prev.pageItemUuid,
          curPage: {
            ...prev.curPage,
            items: prev.curPage.items.map((v) => {
              if (v.uuid !== prev.pageItemUuid) return v;
              return { ...v, itemId: id };
            }),
          },
        };
      });
    },
    []
  );

  const savePage = useCallback((page: Page, saveAs: boolean = false) => {
    setState((prev) => {
      const pages = structuredClone(prev.pages);
      let _page = structuredClone(page);
      if (saveAs) _page.id = uuidv4();
      pages[_page.id] = _page;
      localStorage.setItem(`${APP_PREFIX}-pages`, JSON.stringify(pages));
      return {
        ...prev,
        pages,
        curPage: _page,
        _curPage: _page,
      };
    });
  }, []);

  const toggleItemEditor = useCallback(
    () =>
      setState((prev) => ({
        ...prev,
        isItemEditor: !prev.isItemEditor,
      })),
    []
  );

  const setItemIdInEditor = useCallback(
    (id: string | null) =>
      setState((prev) => ({
        ...prev,
        itemIdInEditor: id || "",
        isItemEditor: true,
      })),
    []
  );

  const saveItem = useCallback(
    (item: Item, saveAs: boolean = false) =>
      setState((prev) => {
        const itemDb = structuredClone(prev.itemDb);
        if (saveAs || item.id === "" || !Boolean(itemDb[item.id])) {
          item.id = uuidv4();
        }
        itemDb[item.id] = item;
        localStorage.setItem(`${APP_PREFIX}-itemDb`, JSON.stringify(itemDb));
        return {
          ...prev,
          itemIdInEditor: item.id,
          itemDb,
        };
      }),
    []
  );

  useEffect(() => {
    if (localStorage.getItem("characters") === null) {
      fetch("https://data.yuedict.app/dict.json")
        .then((r) => r.json())
        .then((db) => {
          const characters = Object.entries(db).reduce((acc, [c, v]) => {
            // @ts-ignore
            acc[c] = v[6][0].map((_v) => _v[0]);
            return acc;
          }, {} as { [char: string]: string[][] });
          localStorage.setItem("characters", JSON.stringify(characters));
          setState((prev) => ({
            ...prev,
            db: characters,
          }));
        });
    }
  }, [setState]);

  return (
    <AppContext.Provider
      value={{
        ...state,
        addWord,
        removeWord,
        backspaceWord,
        clearWords,
        speak,
        loadPage,
        slicePageStack,
        isPageDirty,
        setMode,
        handleDragStart,
        handleDragEnd,
        handleDragOver,
        emptyItemFromPage,
        togglePageEditor,
        openPageItemEditor,
        savePageItem,
        savePage,
        toggleItemEditor,
        setItemIdInEditor,
        saveItem,
      }}
    >
      {children}
    </AppContext.Provider>
  );
};

export default AppContext;

const DEFAULT_STATE: AppContextState = {
  words: ["喝", "蘋果汁"],
  db: JSON.parse(localStorage.getItem("characters") ?? "{}"),
  itemDb: {
    ...JSON.parse(
      localStorage.getItem(`${APP_PREFIX}-itemDb`) ??
        JSON.stringify(DEFAULT_ITEMS)
    ),
    "": {
      id: "",
      text: "",
    },
  },
  pages: JSON.parse(
    localStorage.getItem(`${APP_PREFIX}-pages`) ?? JSON.stringify(DEFAULT_PAGE)
  ),
  pageStack: ["home"],
  mode: "view",
  curDragUuid: null,
  curPage: JSON.parse(
    localStorage.getItem(`${APP_PREFIX}-pages`) ?? JSON.stringify(DEFAULT_PAGE)
  )["home"],
  _curPage: JSON.parse(
    localStorage.getItem(`${APP_PREFIX}-pages`) ?? JSON.stringify(DEFAULT_PAGE)
  )["home"],
  isPageEditor: false,
  pageItemUuid: null,
  isItemEditor: false,
  itemIdInEditor: "",
};
